.agentdetail-wrap {
  display: inline-block;
  position: relative;
  width: 636px;
  height: 80%;
  max-height: 460px;
  padding: 66px;
  margin: 0 20px 0 0;
  color: #fff;
  background-color: rgba(179, 199, 153, 0.89);
  border: 1px solid #fff;
  overflow: hidden;
  overflow-y: scroll; }

.agentdetail-country {
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 3px 0; }

.agentdetail-company {
  margin: 38px 0 0 0;
  font-size: 16px;
  font-weight: 700; }

.agentdetail-address {
  margin: 20px 0 0 0;
  font-size: 14px;
  font-weight: 400; }

.agentdetail-agent {
  margin: 35px 0 0 0;
  font-size: 16px;
  font-weight: 400; }

.agentdetail-agentcontact {
  font-size: 14px;
  font-weight: 400; }

.agentdetail-url {
  margin: 45px 0 0 0;
  font-size: 16px;
  font-weight: 400; }

.agentdetail-rule {
  height: 1px;
  background-color: #fff;
  border-width: 0; }

@media only screen and (max-width: 750px) {
  .agentdetail-wrap {
    position: fixed;
    bottom: 0;
    margin: 0 auto;
    margin-top: 10px;
    padding: 10px;
    height: 300px;
    max-height: auto;
    display: none; }
  .agentdetail-company {
    margin: 12px 0 0 0; }
  .agentdetail-agent {
    margin: 12px 0 0 0; }
  .agentdetail-url {
    margin: 12px 0 0 0; } }



