.navilink-wrap {
  position: relative; }

.navilink-link {
  position: relative;
  padding: 10px 50px 10px 50px;
  color: white;
  font-size: 12px;
  white-space: nowrap;
  letter-spacing: 2px;
  text-decoration: none; }

.navilink-largeText {
  position: absolute;
  display: none;
  top: 0px;
  left: -250px;
  color: rgba(170, 118, 44, 0.2);
  font-family: 'Montserrat', sans-serif;
  font-size: 80px;
  font-weight: bold;
  font-weight: 900;
  opacity: 0;
  pointer-events: none; }

.navilink-description {
  display: none;
  position: absolute;
  top: 35px;
  left: 200px;
  width: 300px;
  color: rgba(255, 255, 255, 0.6);
  white-space: nowrap;
  font-size: 14px;
  text-align: center;
  pointer-events: none;
  opacity: 0; }

.navilink-link:hover ~ .navilink-largeText {
  display: inline-block;
  -webkit-animation: showLinkText 1000ms ease-in-out forwards;
          animation: showLinkText 1000ms ease-in-out forwards; }

.navilink-link:hover ~ .navilink-description {
  display: inline-block;
  -webkit-animation: showLinkText 600ms ease-in-out forwards;
          animation: showLinkText 600ms ease-in-out forwards; }

.navilink-link:visited {
  color: white; }

.navilink-link:hover {
  color: white;
  font-weight: bold; }

.navilink-link:active {
  color: white; }

.navilink-active {
  font-weight: bold; }

.navilink-active:before {
  content: '\2022 '; }

.navilink-active:after {
  content: ' \2022'; }

@-webkit-keyframes showLinkText {
  0% {
    opacity: 0; }
  100% {
    opacity: 1;
    left: -50px; } }

@keyframes showLinkText {
  0% {
    opacity: 0; }
  100% {
    opacity: 1;
    left: -50px; } }

@media screen and (max-width: 950px) {
  .navilink-link {
    padding: 10px 20px 10px 20px; } }

@media screen and (max-width: 750px) {
  .header-nav {
    text-align: right; }
  .navilink-wrap {
    height: 75px; }
  .navilink-link {
    font-size: 35px; }
  .navilink-link a {
    display: block; }
  .navilink-active:before {
    content: ''; }
  .navilink-active:after {
    content: ''; }
  .navilink-link:hover ~ .navilink-largeText {
    display: none; }
  .navilink-link:hover ~ .navilink-description {
    display: none; } }



