.agentsmall-wrap {
  position: relative;
  max-width: 636px;
  height: 80px;
  padding: 20px 20px 10px 20px;
  color: #fff;
  direction: ltr;
  -webkit-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
  transition: all 250ms ease-out;
  border: 1px solid transparent; }

.agentsmall-wrap:hover {
  background-color: rgba(179, 199, 153, 0.89);
  border: 1px solid #fff;
  -webkit-transition: all 450ms ease-in-out;
  -o-transition: all 450ms ease-in-out;
  transition: all 450ms ease-in-out; }

.agentsmall-country {
  letter-spacing: 3px;
  position: relative;
  padding: 0;
  margin: 0 0 4px 0;
  font-size: 16px; }

.agentsmall-copy {
  position: relative;
  padding: 0;
  margin: 6px 0 0 0;
  font-size: 12px; }

.agentsmall-rule {
  height: 1px;
  background-color: rgba(255, 255, 255, 0.3);
  border-width: 0; }

.agentsmall-mobile {
  display: none; }

@media only screen and (max-width: 750px) {
  .agentsmall-wrap {
    height: 250px;
    background-color: rgba(179, 199, 153, 0.89);
    border: 1px solid #fff;
    border-bottom: none; }
  .agentsmall-mobile {
    display: block; }
  .agentsmall-wrap:hover {
    background-color: rgba(179, 199, 153, 0.89);
    border: 1px solid #fff;
    border-bottom: none;
    -webkit-transition: none;
    -o-transition: none;
    transition: none; } }



