.library-wrap {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #54715b;
  background-image: url("../../img/library-bg.jpg");
  background-size: cover;
  overflow: hidden; }

.library-spacer {
  position: relative;
  height: 10%; }

.library-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  position: relative;
  margin: 0; }

.library-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 10% 0 0;
  padding: 0;
  width: 580px;
  height: 390px;
  background-color: rgba(84, 113, 91, 0.82);
  border: 1px solid rgba(157, 214, 169, 0.82); }

.library-copy {
  margin: 50px 0;
  color: #fff;
  font-size: 12px;
  letter-spacing: 1px; }

.library-headline {
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 700; }

.library-headline:hover {
  color: rgba(157, 214, 169, 0.82); }

.library-link {
  display: inline-block; }

.library-link:link {
  color: white;
  text-decoration: underline; }

.library-link:visited {
  color: white; }

.library-link:hover {
  color: rgba(157, 214, 169, 0.82); }

.library-link:active {
  color: white; }

@media only screen and (max-width: 750px) {
  .library-row {
    height: calc(100vh - 90px);
    overflow: hidden;
    overflow-y: scroll; }
  .library-list {
    margin: 0 auto;
    padding: 20px;
    width: 360px;
    height: 390px; } }



